
.vouill--modal {
  position: relative;
  background-color: white;
  margin: auto;
  margin-top: 10%;
  width: 600px;
}
.vouill--modal-bg {
    position: fixed;
    top: 0;
    left: 0;
    height: 100%;
    width: 100%;
    background-color: rgba(0, 0, 0, 0.2);
}
.vouill--modal--header {
    text-align: center;
    width: 100%;
    background-color: #F7F7F7;
    padding: 0.5em 0em;
}
.vouill--modal--header h1 {
      margin: 0;
}
.vouill--modal--content {
    padding: 2em;
    padding-top: 1em;
}
.vouill--modal--content h2 {
      margin: 0;
}
.vouill--modal--action-close {
    position: absolute;
    top: 0.5em;
    right: 0.5em;
    cursor: pointer;
}



