.v-bttn {
  background-color: #27AAE1;
  border: none;
  color: white;
  padding: 1em 1.5em;
  text-align: center;
  transition: all 0.1s;
  box-shadow: 0px 3px 0px #1a8bbb;
  cursor: pointer;
}
.v-bttn:active {
    background-color: #1a8bbb;
    transform: translateY(3px);
    box-shadow: none;
}
.v-bttn--success {
    background-color: #48BB70;
    box-shadow: 0px 3px 0px #389859;
}
.v-bttn--success:active {
      background-color: #389859;
      box-shadow: none;
}
.v-bttn--danger {
    background-color: #F05050;
    box-shadow: 0px 3px 0px #c91111;
}
.v-bttn--danger:active {
      background-color: #c91111;
      box-shadow: none;
}
.v-bttn--warning {
    background-color: #EF8748;
    box-shadow: 0px 3px 0px #c05311;
}
.v-bttn--warning:active {
      background-color: #c05311;
      box-shadow: none;
}
.v-bttn--disabled {
    color: #A4A4A4;
    background-color: #DDDDDD;
    box-shadow: 0px 3px 0px #dddddd;
}
.v-bttn--disabled:active {
      background-color: #aaaaaa;
      box-shadow: none;
}
.v-bttn:disabled {
    color: #A4A4A4;
    background-color: #DDDDDD;
    box-shadow: 0px 3px 0px #dddddd;
}
.v-bttn:disabled:active {
      background-color: #aaaaaa;
      box-shadow: none;
}
